@import 'normalize.css/normalize.css';
@import 'mappy-breakpoints/_mappy-breakpoints.scss';

$breakpoints: (
  'laptop': 1100px,
  'tablet-horiz': 1024px,
  'tablet-vert': 768px,
  'phone-horiz': 736px,
  'phone-horiz-small': 667px,
  'phone-horiz-tiny': 568px,
  'phone-medium': 414px,
  'phone-small': 375px,
  'phone-tiny': 320px
);

@include mappy-bp(tablet-vert) {
  body {
    border: 1px solid blue;
  }
}
